import React from "react"
import { FormItem, Radio } from "formik-antd"
import styled from "styled-components"
import { Form, Radio as AntdRadio } from "antd"

const StyledRadioGroup = styled(Radio.Group)`
	> label {
		display: ${({ labelBlock }) => labelBlock && "block"};
		width: ${({ labelBlock }) => labelBlock && "100%"};
	}
`

const AntRadioGroup = styled(AntdRadio.Group)`
	> label {
		display: ${({ labelBlock }) => labelBlock && "block"};
		width: ${({ labelBlock }) => labelBlock && "100%"};
		margin-bottom: ${({ labelBlock }) => labelBlock && "1em"};
		padding: ${({ labelBlock }) => labelBlock && "1em"};
		height: ${({ labelBlock }) => labelBlock && "auto"};
	}
`

function RadioInput({ options = [], name, label, independent, isButton = true, ...props }) {
	if (independent) {
		return (
			<Form.Item name={name} label={label}>
				<AntRadioGroup {...props} name={name} buttonStyle="solid">
					{options.map((item) => (
						<AntdRadio.Button {...item} value={item.value} key={item.value}>
							{item.label}
						</AntdRadio.Button>
					))}
				</AntRadioGroup>
			</Form.Item>
		)
	}

	return (
		<FormItem name={name} label={label}>
			{isButton ? (
				<StyledRadioGroup {...props} name={name} buttonStyle="solid">
					{options.map((item) => (
						<Radio.Button value={item.value} key={item.value}>
							{item.label}
						</Radio.Button>
					))}
				</StyledRadioGroup>
			) : (
				<StyledRadioGroup {...props} options={options} name={name} />
			)}
		</FormItem>
	)
}

export default RadioInput
